<template>
  <div>
    <el-card>
      <div slot="header">
        基本信息
        <el-button
          style="float: right"
          type="info"
          size="small"
          @click="$router.go(-1)"
          >返回</el-button
        >
      </div>
      <div>
        <!-- :rules="rules" -->
        <el-form
          :model="Form"
          ref="formRef"
          :rules="rule"
          label-width="150px"
          class="demo-ruleForm"
        >
          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="负责人" required prop="peoId">
                <el-select
                  v-model="Form.peoId"
                  filterable
                  placeholder="请输入内容"
                >
                  <el-option
                    v-for="item in Options"
                    :key="item.id"
                    :label="item.teaName"
                    :value="item.teaId"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label="学科门类" required prop="subjectCategory">
                <el-input v-model="Form.subjectCategory"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="论文题目" required prop="paperName">
                <el-input v-model="Form.paperName"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="论文类型" required prop="paperType">
                <el-input v-model="Form.paperType"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item
                label="发表/出版时间"
                required
                prop="paperPublishTime"
              >
                <el-input v-model="Form.paperPublishTime"></el-input>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item
                label="发表刊物/论文集"
                required
                prop="paperPublication"
              >
                <el-input v-model="Form.paperPublication"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item
                label="刊物类型"
                required
                prop="paperPublicationType"
              >
                <el-input v-model="Form.paperPublicationType"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item v-if="this.istea == 1" label="所属单位" prop="crBelongDept">
                <el-input v-model="Form.belongDept"></el-input>
              </el-form-item>
              <el-form-item v-else label="所属学院" prop="crBelongDept">
                <el-input v-model="Form.belongDept"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item
                label="一级学科"
                required
                prop="firstlevelDiscipline"
              >
                <el-input v-model="Form.firstlevelDiscipline"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="项目来源" required prop="projectSource">
                <el-input v-model="Form.projectSource"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="发表范围" required prop="publicationScope">
                <el-input v-model="Form.publicationScope"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="卷号" required prop="volumeNumber">
                <el-input v-model="Form.volumeNumber"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="期号" required prop="issueNumber">
                <el-input v-model="Form.issueNumber"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="页码范围" required prop="pageRange">
                <el-input v-model="Form.pageRange"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item
                label="字数（单位：万字）"
                required
                prop="wordCount"
              >
                <el-input v-model="Form.wordCount"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item
                label="学校署名"
                required
                prop="schoolSignature"
              >
                <el-select v-model="Form.schoolSignature">
                  <el-option label="第一单位" value="第一单位"></el-option>
                  <el-option label="非第一单位" value="非第一单位"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item
                label="版面"
                required
                prop="paperLayout"
              >
                <el-select v-model="Form.paperLayout">
                  <el-option label="正刊" value="正刊"></el-option>
                  <el-option label="增刊" value="增刊"></el-option>
                  <el-option label="年刊" value="年刊"></el-option>
                  <el-option label="专利" value="专利"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item
                label="是否为译文"
                required
                prop="isTranslationPaper"
              >
              <el-select v-model="Form.isTranslationPaper">
                  <el-option label="是" value="是"></el-option>
                  <el-option label="否" value="否"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="ISSN号" required prop="issnNumber">
                <el-input v-model="Form.issnNumber"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="CN号" required prop="cnNumber">
                <el-input v-model="Form.cnNumber"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="关键词" required prop="paperKeywords">
                <el-input v-model="Form.paperKeywords"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="摘要" prop="paperAbstract">
                <el-input v-model="Form.paperAbstract"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="备注" prop="remarksInfo">
                <el-input v-model="Form.remarksInfo"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </div>
    </el-card>

    <el-card>
      <div slot="header">
        作者信息
        <!-- 新增 -->
        <span>
          <el-tooltip
            effect="dark"
            content="新增"
            placement="right"
            :enterable="false"
          >
            <!-- v-if="is_authority.is_userDel" -->
            <el-button
              class="add"
              type="primary"
              icon="el-icon-plus"
              v-if="is_authority.is_user_add"
              size="mini"
              @click="authAdd"
              >点击增加</el-button
            >
          </el-tooltip>
        </span>
      </div>
      <div>
        <el-table :data="authForm" border stripe>
          <el-table-column prop="authorType" label="作者类型" align="center" width="150px">
            <template slot-scope="scope">
              <el-select v-model="scope.row.authorType">
                <el-option
                  label="本校老师"
                  value="本校老师">
                </el-option>
                <el-option
                  label="本校学生"
                  value="本校学生">
                </el-option>
                <el-option
                  label="外校人员"
                  value="外校人员">
                </el-option>
              </el-select>
            </template>
          </el-table-column>

          <el-table-column prop="authorName" label="* 作者姓名" align="center">
            <template slot-scope="scope">
              <el-input v-model="scope.row.authorName"></el-input>
            </template>
          </el-table-column>

          <el-table-column
            width="150"
            prop="authorId"
            label="作者编号"
            align="center"
          >
            <template slot-scope="scope">
              <el-input v-model="scope.row.authorId"></el-input>
            </template>
          </el-table-column>

          <el-table-column
            width="80"
            prop="authorSex"
            label="性别"
            align="center"
          >
            <template slot-scope="scope">
              <el-input v-model="scope.row.authorSex"></el-input>
            </template>
          </el-table-column>

          <el-table-column prop="authorEducation" label="学历" align="center">
            <template slot-scope="scope">
              <el-select v-model="scope.row.authorEducation" placeholder="请选择学历">
                  <el-option
                    label="博士研究生"
                    value="博士研究生">
                  </el-option>
                  <el-option
                    label="硕士研究生"
                    value="硕士研究生">
                  </el-option>
                  <el-option
                    label="本科"
                    value="本科">
                  </el-option>
                  <el-option
                    label="专科"
                    value="专科">
                  </el-option>
                  <el-option
                    label="高中及其以下"
                    value="高中及其以下">
                  </el-option>
                  <el-option
                    label="其他"
                    value="其他">
                  </el-option>
                </el-select>
            </template>
          </el-table-column>

          <el-table-column
            width="100"
            prop="signingOrder"
            label="* 署名顺序"
            align="center"
          >
            <template slot-scope="scope">
              <el-input
                v-model="authForm[scope.$index].signingOrder"
              ></el-input>
            </template>
          </el-table-column>

          <el-table-column v-if="this.istea == 1" prop="authorTitle" label="职称" align="center">
            <template slot-scope="scope">
              <el-input v-model="scope.row.authorTitle"></el-input>
            </template>
          </el-table-column>

          <el-table-column
            prop="authorWorkUnit"
            label="是否通信作者"
            align="center"
          >
            <template slot-scope="scope">
              <el-select v-model="scope.row.isCorrespondingAuthor">
                <el-option
                  label="是"
                  value="是">
                </el-option>
                <el-option
                  label="否"
                  value="否">
                </el-option>
              </el-select>
            </template>
          </el-table-column>

          <el-table-column
            prop="isCorrespondingAuthor"
            label="工作单位"
            align="center"
          >
            <template slot-scope="scope">
              <el-input v-model="scope.row.authorWorkUnit"></el-input>
            </template>
          </el-table-column>

          <el-table-column
            prop="contributionRate"
            label="贡献率：（ ）单位：%"
            align="center"
            ><template slot-scope="scope">
              <el-input
                v-model="scope.row.contributionRate"
              ></el-input> </template
          ></el-table-column>

          <el-table-column label="操作" width="120px" align="center">
            <template slot-scope="scope">
              <!-- 删除 -->
              <el-tooltip
                class="item"
                effect="dark"
                content="删除"  v-if="is_authority.is_user_del"
                placement="top-start"
                :enterable="false"
              >
                <!-- v-if="is_authority.is_userDel" -->
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="delAuthRow(scope, authForm)"
                ></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <el-button class="save" type="primary" @click="submitForm"
        >保存
      </el-button>
    </el-card>

    <el-card>
      <div slot="header">
        附件信息

        <el-upload
          :file-list="fileList"
          class="upload-demo"
          action="http://www.dayanhulian.com:8001/matter/fileoss/uploadOssFile"
          multiple
          :headers="config"
          :on-success="saveUrl"
          :before-upload="beforeUpload"
          accept=".pdf,.doc,.docx"
        >
          <el-button icon="el-icon-upload2" size="small" type="primary"
            >点击上传</el-button
          >
          <span slot="tip" class="el-upload__tip">只能上传pdf/word文件</span>
        </el-upload>
      </div>
      <div
      v-if="this.Id"
      >
        <el-table :data="accessory" border stripe>
          <el-table-column label="附件" align="center">
            <template slot-scope="scope">
              <a :href="scope.row.url">{{ scope.row.url.substring(83) }}</a>
            </template>
          </el-table-column>

          <el-table-column label="操作" width="120px" align="center">
            <template slot-scope="scope">
              <el-tooltip
                v-if="scope.row.url.slice(-4) == '.pdf'"
                class="item"
                effect="dark"
                content="预览"
                placement="top"
                :enterable="false"
              >
                <a :href="scope.row.url">
                  <el-button
                    class="download"
                    type="primary"
                    icon="el-icon-view"
                    size="mini"
                  >
                  </el-button>

                  
                </a>
              </el-tooltip>

              <el-tooltip
                v-else
                class="item"
                effect="dark"
                content="下载"
                placement="top"
                :enterable="false"
              >
                <a :href="scope.row.url">
                  <el-button
                    class="download"
                    type="primary"
                    icon="el-icon-download"
                    size="mini"
                  ></el-button>

                  
                </a>
              </el-tooltip>

              

              <el-tooltip
                class="item"
                effect="dark"
                content="删除"  v-if="is_authority.is_user_del"
                placement="top"
                :enterable="false"
              >
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="delAccessory(scope, accessory)"
                ></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-card>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  data() {
    return {
      is_authority: {
        is_user_del: false,
        is_user_upt: false,
        is_user_add: false,
        is_user_add_role: false
      },
      rule:{
        peoId: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        subjectCategory: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        paperName: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        paperType: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        paperPublishTime: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        paperPublication: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        paperPublicationType: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        crBelongDept: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        firstlevelDiscipline: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        projectSource: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        publicationScope: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        volumeNumber: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        issueNumber: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        pageRange: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        wordCount: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        schoolSignature: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        paperLayout: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        isTranslationPaper: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        issnNumber: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        cnNumber: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        paperKeywords: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        paperAbstract: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        paperLayout: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ]
      },
      fileList: [],
      accessory: [],
      Options: [],
      Form: {},
      authForm: [],
      List: [],
      istea: this.$route.query.istea,
      token: window.sessionStorage.getItem('token'),
      Id: this.$route.query.id,
      newAddId: undefined
    }
  },
  computed: {
    ...mapState(['btus']),
    config() {
      return { Token: this.token }
    }
  },
  created() {
    this.isAuthority()
    this.getDetail()
    this.getTreeselect()
  },
  methods: {
    isAuthority() {
      var arr = this.btus.split(',')
      for (var key in this.is_authority) {
        if (arr.indexOf(key) != -1) {
          this.is_authority[key] = true
        }
      }
    },
    beforeUpload(file){
      console.log(file.type);
      if (!this.Id && !this.newAddId) {
        this.$message.error('请先保存作者信息!')
        return false
      }else{
        const isPFD = file.type === 'application/pdf' || file.type ==='application/msword'|| file.type === 'application/vnd.openxmlformats-officedocument.wordprocessingml.document';
        console.log(isPFD);
        if (!isPFD) {
          this.$message.error('请上传 PFD或者WORD 格式!');
        return isPFD
        }
      }
    },
    async saveUrl(response, file, fileList) {
      this.$http.post('/accessoriesList/saveOrUpdateAccessoriesList', {
        proId: this.Id || this.newAddId,
        url: response.data.url
      })
      this.getDetail()
    },

    authAdd() {
      this.authForm.splice(this.authForm.length, 0, {
        authorEducation: '',
        authorId: '',
        authorName: '',
        authorSex: '',
        authorTitle: '',
        authorType: '',
        authorWorkUnit: '',
        contributionRate: '',
        id: '',
        isCorrespondingAuthor: '',
        paperId: this.$route.query.id,
        signingOrder: ''
      })
    },

    delAccessory(scope, rows) {
      const userid = scope.row.id.split(',')
      console.log(userid)
      this.$confirm('确认要删除吗?', '警告', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          await this.$http.delete(
            '/accessoriesList/removeByIdAccessoriesList/' + userid
          )
          // console.log(res);
          this.getDetail()
        })
        .then(() => {
          this.$message({
            showClose: true,
            message: '删除成功',
            type: 'success'
          })
        })
    },

    delAuthRow(scope, rows) {
      // console.log(scope);
      if (scope.row.id) {
        const userid = scope.row.id.split(',')
        console.log(userid)
        this.$confirm('确认要删除吗?', '警告', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
          .then(async () => {
            await this.$http.delete(
              '/paperAuthorInfo/removeByIdPaperAuthorInfos',
              {
                data: userid
              }
            )
            // console.log(res);
            this.getDetail()
            this.Form = []
          })
          .then(() => {
            this.$message({
              showClose: true,
              message: '删除成功',
              type: 'success'
            })
          })
      } else {
        rows.splice(scope.$index, 1)
      }
    },

    async getTreeselect() {
      this.istea = this.$route.query.istea
      const id = this.$route.query.id
      // console.log(istea);
      if (this.istea == 1) {
        const { data: res } = await this.$http.post(
          '/teaBaseInfo/queryTeaBaseInfoList',
          {
            page: {
              current: 1,
              size: 999
            }
          }
        )
        this.Options = res.data.dataList.records
        if (res.code != 20000) return this.$message.error(res.message)
        // console.log(this.Options)
        // console.log(this.majorOptions)
      } else if (this.istea == 0) {
        const { data: res } = await this.$http.post(
          '/studentInfo/queryStudentInfoList',
          {
            page: {
              current: 1,
              size: 999
            }
          }
        )
        let a = res.data.dataList.records
        a = JSON.parse(JSON.stringify(a).replace(/stuId/g, 'teaId'))
        this.Options = JSON.parse(
          JSON.stringify(a).replace(/stuName/g, 'teaName')
        )
        if (res.code != 20000) return this.$message.error(res.message)
        // console.log(this.Options)
        // console.log(this.majorOptions)
      }
    },

    async getDetail() {
      // console.log(this.Id);
      if (!this.Id) {
        return
      }
      // console.log(id)
      const { data: res } = await this.$http.get(
        '/paperInfo/getByIdPaperInfo/' + this.Id
      )
      if (res.code != 20000) return this.$message.error(res.message)
      this.Form = res.data.entity
      console.log(this.Form)
      this.getAutherinfo()
      this.getAccessory()
    },

    async getAccessory() {
      const { data: res } = await this.$http.get(
        '/accessoriesList/getByIdAccessoriesList/' + this.Form.id
      )
      if (res.code != 20000) return this.$message.error(res.message)
      this.accessory = res.data.entity
      console.log(this.accessory)
    },

    async getAutherinfo() {
      const { data: res } = await this.$http.get(
        '/paperAuthorInfo/queryPaperAuthorInfoList/' + this.Form.id
      )
      if (res.code != 20000) return this.$message.error(res.message)
      this.authForm = res.data.dataList
      console.log(this.authForm)
    },
    submitForm() {
      if (this.istea == 1) this.Form.isStu = 1
      else if (this.istea == 0) this.Form.isStu = 0
      this.$refs.formRef.validate(async valid => {
        if (valid) {
          for (let index = 0; index < this.authForm.length; index++) {
            if (!this.authForm[index].authorName  || !this.authForm[index].signingOrder) {
              return this.$message.error('请输入作者姓名和作者署名')
            }

          }
          await this.$http.post('/paperInfo/saveOrUpdatePaperInfo', this.Form)

          if (!this.Id && this.istea == 1) {
            const { data: res } = await this.$http.post(
              '/paperInfo/queryTeaPaperInfoList',
              {
                page: {
                  current: 1,
                  size: 999
                }
              }
            )
            this.newAddId = res.data.dataList.records.pop().id
            // console.log(Id)
          } else if (!this.Id && this.istea == 0) {
            const { data: res } = await this.$http.post(
              '/paperInfo/queryStuPaperInfoList',
              {
                page: {
                  current: 1,
                  size: 999
                }
              }
            )
            this.newAddId = res.data.dataList.records.pop().id
          }

          for (let index = 0; index < this.authForm.length; index++) {
            if (!this.Id) {
              this.authForm[index].paperId = this.newAddId
              this.authForm[index].signingOrder = 1
            }
            var { data: res1 } = await this.$http.post(
              '/paperAuthorInfo/saveOrUpdatePaperAuthorInfo',
              this.authForm[index]
            )
          }
          if(!res1) return this.$message.error('请填写作者信息!')
          if (res1.code != 20000) return this.$message.error(res1.message)
          else {
            return this.$message.success(res1.message)
            this.getDetail()
            this.$router.go(-1)
          }
        }
      })
    }
  }
}
</script>
